.dashboard-page {
  padding: 20px;
}

.welcome {
  height: 270px;
  box-shadow: 2px 4px 15px 3px rgba(0, 0, 0, 0.2);
  background-image: url("../../img/welcome.webp");
  background-size: cover;
  background-repeat: no-repeat;
  visibility: none;
}

.welcome-overlay {
  height: 270px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 100;
}

.welcome-content {
  padding: 10px;
  padding-top: 30px;
  padding-left: 30px;
  margin-left: -30px;
  z-index: 200;
  text-align: center;
}

.welcome-content h1 {
  color: white;
  margin-top: 0;
}

.welcome-content h2 {
  color: white;
  font-weight: 300;
}

.build {
  margin-top: 30px;
  background-color: transparent;
  width: 100px;
  height: 45px;
  border: 1px solid white;
  color: white;
  cursor: pointer;
}

.build:hover {
  border: 1px solid #5f0a87;
  background-color: #5f0a87;
  background-image: linear-gradient(326deg, #7818a4 20%, #5f0a87 80%);
}

.dashboard-rankings-container {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  justify-content: space-around;
}

.dashboard-page > h1 {
  margin: 40px 0 30px 30px;
  color: #5f0a87;
}

h6 {
  margin: 10px 0 10px 0;
  color: white;
  font-weight: 300;
}

@media screen and (max-width: 1400px) {
  .welcome-content {
    margin-left: 0;
  }
}

@media screen and (max-width: 1000px) {
  .welcome-content {
    padding: 10px;
  }
}

@media screen and (max-width: 800px) {
  .welcome-content {
    transform: translate(0);
  }
}

@media screen and (max-width: 425px) {
  .welcome,
  .welcome-overlay {
    height: 330px;
    width: 100vw;
  }

  .welcome-content h2 {
    margin: 5px 0 5px 0;
  }

  .welcome-content h2 {
    margin: 2px 0 2px 0;
  }

  .build {
    margin: 0;
  }

  .dashboard-page {
    padding: 0;
  }
}
